.contacts__img {
  position: absolute;
  top: 0;
  left: 0;
}
@media (max-width: 767px) {
  .contacts__img {
    width: 80%;
    max-width: 382px;
  }
}

.contacts__img-bg {
  position: absolute;
  top: 170px;
  left: 0;
  right: 0;
  width: 817px;
}
@media (max-width: 1023px) {
  .contacts__img-bg {
    width: 90%;
    top: 170px;
  }
}
@media (max-width: 767px) {
  .contacts__img-bg {
    top: 400px;
  }
}

.contacts__wrapper {
  padding-bottom: 115px;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  padding-right: 8.5%;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    padding-right: 0;
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
.contacts__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: -40px;
  background-color: #0f264c;
  border-top-right-radius: 100px 100px;
  z-index: -1;
}
@media (max-width: 1023px) {
  .contacts__wrapper::before {
    right: -24px;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper::before {
    right: -16px;
  }
}
.contacts__wrapper::after {
  content: "";
  position: absolute;
  background-color: #0f264c;
  top: 0;
  bottom: 0;
  right: calc(100% - 1px);
  width: calc((100vw - 100%) / 2 + 1px);
  z-index: -1;
}

.contacts__description {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: #ffffff;
  margin: 0;
  margin-top: 100px;
  max-width: 380px;
  padding: 20px 0;
  padding-left: 40px;
  border-left: 10px solid #ed4d47;
  border-radius: 6px;
  align-self: start;
}
@media (max-width: 1023px) {
  .contacts__description {
    order: 2;
    margin-top: 0;
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .contacts__description {
    width: auto;
    max-width: none;
  }
}

.contacts__form {
  position: relative;
  top: -110px;
  margin-bottom: 57px;
}
@media (max-width: 1023px) {
  .contacts__form {
    order: 1;
    margin-bottom: 0;
  }
}

.contacts__nav {
  grid-column: 1/-1;
  order: 3;
  justify-self: stretch;
  display: flex;
  justify-content: space-between;
}
.contacts__nav svg {
  transition: fill 0.3s ease;
}
@media (hover: hover) {
  .contacts__nav:hover:not(.focus-visible) {
    outline: none;
  }
  .contacts__nav:hover:not(.focus-visible) svg {
    fill: rgba(255, 255, 255, 0.8);
  }
}
.contacts__nav.focus-visible:focus {
  outline: none;
}
.contacts__nav.focus-visible:focus svg {
  fill: rgba(255, 255, 255, 0.8);
}
@media (max-width: 767px) {
  .contacts__nav {
    flex-direction: column;
    row-gap: 30px;
    align-items: center;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  column-gap: 104px;
}
@media (max-width: 767px) {
  .contacts__list {
    max-width: 375px;
    width: 100%;
    column-gap: 0;
    justify-content: space-between;
    order: -1;
  }
}

.contacts__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  display: block;
  transition: outline 0.3s ease, background-color 0.3s ease, color 0.3s ease;
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    color: #ed4d47;
    outline: none;
  }
}
.contacts__link.focus-visible:focus {
  color: #ed4d47;
  outline: none;
}

.form {
  background-color: #ffffff;
  width: 550px;
  border-radius: 10px;
}
@media (max-width: 767px) {
  .form {
    width: 95%;
  }
}
.form form {
  padding: 64px 75px 52px;
}
@media (max-width: 767px) {
  .form form {
    padding: 64px 40px 52px;
  }
}
@media (max-width: 374px) {
  .form form {
    padding: 64px 15px 52px;
  }
}

.form__input {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}
.form__input input:not([type=checkbox]) {
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  color: #402b2b;
  font-family: "Rubik", "Arial", sans-serif;
  height: 60px;
  width: 100%;
  padding: 0 40px;
  border: 1px solid rgba(237, 77, 71, 0.5);
  border-radius: 5px;
}
.form__input input:not([type=checkbox])::placeholder {
  font-weight: 300;
  font-size: 18px;
  line-height: 1;
  color: #402b2b;
  font-family: "Rubik", "Arial", sans-serif;
}
@media (hover: hover) {
  .form__input input:not([type=checkbox]):hover:not(.focus-visible) {
    outline: 1px solid #ed4d47;
    box-shadow: 0px 20px 40px 0px rgba(238, 77, 71, 0.2);
  }
}
.form__input input:not([type=checkbox]).focus-visible:focus {
  outline: 1px solid #ed4d47;
  box-shadow: 0px 20px 40px 0px rgba(238, 77, 71, 0.2);
}
@media (max-width: 767px) {
  .form__input input:not([type=checkbox]) {
    padding: 0 20px;
  }
}
.form__input label {
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  color: #402b2b;
  align-self: start;
}

.form__input:not(:last-of-type) {
  margin-bottom: 42px;
}

.form__input:last-of-type {
  margin-bottom: 30px;
}

.form__submit {
  padding: 21px 78px 21px 25px;
  position: relative;
}
@media (hover: hover) {
  .form__submit:hover:not(.focus-visible) .form__submit-icon {
    fill: #ed4d47;
    right: 19px;
  }
}
.form__submit.focus-visible:focus .form__submit-icon {
  fill: #ed4d47;
  right: 19px;
}

.form__submit-icon {
  position: absolute;
  right: 24px;
  top: 50%;
  transform: translateY(-50%);
  transition: fill 0.3s ease, right 0.3s ease;
}

.main-footer {
  padding: 45px 0 94px;
}

.main-footer__wrapper {
  display: flex;
  column-gap: 20px;
  justify-content: space-between;
  align-items: center;
  width: 91.6%;
}
.main-footer__wrapper > p {
  font-weight: 300;
  font-size: 16px;
  line-height: 1;
  color: #402b2b;
}
@media (max-width: 767px) {
  .main-footer__wrapper > p {
    order: 2;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    width: 100%;
    flex-direction: column;
  }
}

.main-footer__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 31px;
}
@media (max-width: 767px) {
  .main-footer__social-list {
    order: 1;
  }
}

.main-footer__social-link {
  display: block;
}
@media (hover: hover) {
  .main-footer__social-link:hover:not(.focus-visible) svg {
    fill: rgba(237, 77, 71, 0.7);
  }
}
.main-footer__social-link.focus-visible:focus svg {
  fill: rgba(237, 77, 71, 0.7);
}
.main-footer__social-link svg {
  display: block;
  transition: fill 0.3s ease;
}

